// Core
import React, { FC } from "react";
import { Collapse, Checkbox, Typography } from "antd";
import { CheckboxChangeEvent } from "antd/lib/checkbox";

const Collapses: FC = () => {
  const { Panel } = Collapse;
  const { Paragraph } = Typography;

  function onChange(event: CheckboxChangeEvent) {
    console.log(`checked = ${event.target.checked}`);
  }

  const collapseStyles = {
    fontSize: "1.5em",
    marginTop: ".4em",
    marginBottom: ".2em",
  };

  return (
    <>
      <Paragraph style={collapseStyles}>Фильтры</Paragraph>
      <Collapse>
        <Panel header="Статус" key="1">
          <Checkbox style={{ margin: 0 }} onChange={onChange}>
            Checkbox 1
          </Checkbox>

          <Checkbox style={{ margin: 0 }} onChange={onChange}>
            Checkbox 2
          </Checkbox>
          <Checkbox style={{ margin: 0 }} onChange={onChange}>
            Checkbox 3
          </Checkbox>
          <Checkbox style={{ margin: 0 }} onChange={onChange}>
            Checkbox 4
          </Checkbox>
        </Panel>
      </Collapse>
    </>
  );
};

export { Collapses };
