// Core
import React, { FC } from "react";
import { Table } from "antd";
import { ColumnProps } from "antd/es/table";

// Models
import { addClient } from "../../models/client";
import { toggleModal } from "../../models/modal";

// Styles
import "./index.css";

// Types
import { Data } from "./types";

type MainTablePropsTypes = {
  columns: ColumnProps<Data>[];
  data: Data[];
};

const MainTable: FC<MainTablePropsTypes> = (props: MainTablePropsTypes) => {
  const { columns, data } = props;

  return (
    <Table<Data>
      className="table"
      columns={columns}
      dataSource={data}
      tableLayout="fixed"
      bordered
      style={{
        // boxShadow: "0 0 0.2em #ffff00",
      }}
      scroll={{ y: '55vh' }}
      pagination={{ pageSize: 6, style: { margin: "1em" } }}
      onRow={(record) => {
        return {
          onClick: (event) => {
            addClient(record);
            toggleModal(true);
          },
        };
      }}
    />
  );
};

export { MainTable };
