// Core
import { createStore, createEvent } from "effector";

// Types
import { Data } from "../../components/MainTable/types";

// Store
export const $client = createStore<Data>({
  id: "",
  createdTime: "",
  createdDate: "",
  status: "",
  client: "",
  phone: "",
  price: "",
  barber: "",
});

// Events
export const addClient = createEvent<Data>();

// Actions
$client.on(addClient, (state, client) => client);
